<template>
    <div class="info-container" style="height: calc(100% - 0em);margin: 0;padding:0;width:100%">
        <div class="info-div" style="height:100%">
            <div class="list-content  con-left" >
                <div class="list-header">
                  <i class="hasIcon hasImg">
                    <img src="dist/img/face/sh.png" />
                  </i>
                  角色列表
                </div>
                <div class="con_pe">
                    <ul>
                        <li v-for= "(roleItem, $index) in roleInfo"
                            :class="{'rowselect': roleItem.id == roledata.id, '':roleItem.id != roledata.id}"
                            @click="selRole(roleItem)">
                            {{roleItem.roleName}}
                        </li>
                        <li v-if="roleEidtState"  class="noHover">
                            <input class="role-input" type="text" v-model="roledata.roleName"    maxlength="10"/>
                            <button type='button' class='btn btn-default add'  @click="addRole">增加</button>
                            <button type='button' class='btn btn-default add'  @click="cancelRoleEdit">取消</button>
                        </li>
                    </ul>
                </div>
              <div class="con_ico">
                <!--controls[1] controls[3]-->
                <button v-if="controls[1]" class="button-default button-operate-role"  style="" id="add" @click="addRoleView" >
                  <i class="hasIcon hasImg" >
                    <img src="dist/img/configuration/add-new-button.png" />
                  </i>新增角色
                </button>
                <button v-if="controls[3]" class="button-default button-operate-role" id="delete" @click="deleteRole" data-toggle="modal" >
                  <i class="hasIcon hasImg" >
                    <img src="dist/img/configuration/delete-button.png"  />
                  </i>删除角色
                </button>
              </div>
            </div>
            <div id="permiss_opt" class="list-content con_center" style="width:calc(80% - 3.5em);position:relative" >
                <!-- permissOpt.editstate===false && controls[2]     permissOpt.editstate &&  !roleEidtState && controls[2]-->
                <button class="list-operate sssb" v-if=" ((permissOpt.editstate === false || (permissOpt.editstate && !roleEidtState)) && controls[2] && roleInfo.length > 0)" id="fix" @click=" editRolePeimissView(permissOpt.editstate && !roleEidtState) ">
                  <i class="hasIcon " style="margin-top:-0.2em"><icon name="edit"></icon></i>
                     {{permissOpt.editstate && !roleEidtState ? '取消' : '编辑' }}
                </button>
                <button class="list-operate sssb" v-if="permissOpt.editstate &&  !roleEidtState && controls[2]" @click="editRolePeimiss">
                  <i class="hasIcon " style="margin-top:-0.2em"><icon name="save"></icon></i>提交
                </button>
                <ul class="titleUl">
                  <li>
                    <span class="tab_left spanTitle" >菜单名称</span>
                    <span class="tab_right spanTitle" > 菜单操作</span>
                  </li>
                </ul>
                <VuePerfectScrollbar class="MenuUi">
                      <li v-for="(menuItem,$index) in permissOpt.menuData">
                         <div class="fatherChild">
                            <span :class="{'typeParent ': menuItem.parentMenuId === 0,  'nodeOpenFolder': true,  'imgSpan': true}"></span>
                            <span>{{menuItem.menuTitle}}</span>
                         </div>
                          <ul>
                              <li v-for="(cItems ,$cIndex) in menuItem.children" v-if="((!(cItems.children && cItems.children.length > 0) && getCheckByOpt(cItems.menuId, null ) || (cItems.children && cItems.children.length > 0)) || permissOpt.editstate) ">
                                  <div class="tab tab_left">

                                      <span :class="{ typeParent:cItems.children && cItems.children.length > 0, 'typeChild ':!(cItems.children && cItems.children.length > 0), 'nodeOpenFolder': cItems.children && cItems.children.length > 0, 'nodeFile':!(cItems.children && cItems.children.length > 0), 'imgSpan': true}"></span>
                                      <span>{{cItems.menuTitle}}</span>

                                  </div>
                                 <template v-if="cItems.children && cItems.children.length > 0">
                                     <ul>
                                        <li  v-for="(leaf ,$cIndex) in cItems.children" v-if="getCheckByOpt(leaf.menuId, null ) || permissOpt.editstate">
                                              <div class="tab tab_left">
                                                  <span :class="{ 'typeChild ':true, 'nodeFile':true, 'imgSpan': true}"></span>
                                                    <span>{{leaf.menuTitle}}</span>
                                              </div> 
                                               <div class="tab_right" >
                                                   <div class="tab_center"   v-if="permissOpt.editstate" >
                                                       <input type="checkbox"  class="checkBox" menuId="" :id="leaf.menuId + 'P'" style="float:left" @click="selectAll(leaf, $event)" checked="" />
                                                       {{getParentCheck(leaf.menuId)}}
                                                       <span :class="{inputspan :!(permissOpt.editstate && !roleEidtState), inputChoose :permissOpt.editstate, myCheckbox: permissOpt.editstate, myCheckBox: permissOpt.editstate, totalCheckLabel: true}">全选</span>
                                        
                                                  </div>
                                                 <div class="tab_content" :id="leaf.menuId">
                                                    <span v-for="(item,$index) in permissOpt.perActionData" v-if="item.menuId === leaf.menuId && (permissOpt.editstate || getCheckByOpt(leaf.menuId,item.actionId))">
                                                        <input type="checkbox" :id="leaf.menuId +'' + item.actionId " :menuId="leaf.menuId" :optid="item.actionId" v-if="permissOpt.editstate" class="checkBox"  style="float:left" :checked="getCheckByOpt(leaf.menuId,item.actionId)"  @click="setStatus(leaf.menuId, $event)" />
                                                        <span :class="{inputspan :!(permissOpt.editstate && !roleEidtState), inputChoose :permissOpt.editstate, myCheckbox: permissOpt.editstate, myCheckBox: permissOpt.editstate}">{{item.actionName}}</span>
                                                     </span>
                                                 </div>
                                            </div>
                                        </li>
                                     </ul>
                                 </template>
                                 <template v-else>
                                    <div class="tab_right" style=" margin-left: calc(1.6em * 0.6);">
                                     <div class="tab_center"  v-if="permissOpt.editstate" >
                                          <input type="checkbox"  class="checkBox" menuId="" :id="cItems.menuId + 'P'" style="float:left" @click="selectAll(cItems, $event)" checked="" />
                                          {{getParentCheck(cItems.menuId)}}
                                          <span :class="{inputspan :!(permissOpt.editstate && !roleEidtState), inputChoose :permissOpt.editstate, myCheckbox: permissOpt.editstate, myCheckBox: permissOpt.editstate, totalCheckLabel: true}">全选</span>
                                        
                                     </div>
                                     <div class="tab_content" :id="cItems.menuId">
                                         <span v-for="(item,$index) in permissOpt.perActionData" v-if="item.menuId === cItems.menuId && (permissOpt.editstate || getCheckByOpt(cItems.menuId,item.actionId))">
                                              <input type="checkbox" :id="cItems.menuId +'' + item.actionId " :menuId="cItems.menuId" :optid="item.actionId" v-if="permissOpt.editstate" class="checkBox"  style="float:left" :checked="getCheckByOpt(cItems.menuId,item.actionId)"  @click="setStatus(cItems.menuId,$event)" />
                                              <span :class="{inputspan :!(permissOpt.editstate && !roleEidtState), inputChoose :permissOpt.editstate, myCheckbox: permissOpt.editstate, myCheckBox: permissOpt.editstate}">{{item.actionName}}</span>
                                        </span>
                                    </div>
                                  </div>
                                 </template>
                                  
                              </li>
                          </ul>
                      </li>
                </VuePerfectScrollbar>
            </div>
        </div>
      <dialogs type="alert" ref="alerts"></dialogs>
      <dialogs type="confirm" ref="confirms"></dialogs>
    </div>
</template>
<script>
    export default{
        data () {
            return {
                newRole: '',
                parentId: [],
                controls: [],
                myDialog: '',
                dialogId: 'msgDialog',
                msgs: '',
                local: '',
                state: '',
                deleteOpe: '',
                userId: '',
                //当前操作的角色
                roledata: {
                    id: '', //角色ID
                    roleName: '' //角色名称
                },
                permissOpt: {
                    //角色编辑状态
                    editstate: false,
                    //操作集合 permissOpt.perActionData
                    perActionData: [
                        {actionId: 1, actionName: '添加'}],
                    //菜单集合 permissOpt.menuData
                    menuData: [],
                    //菜单权限 permissOpt.menuPei
                    menuPei: [{menuId: 1, actionId: 1}]
                },
                roleEidtState: false,//角色编辑状态
                //系统全部角色
                roleInfo: [],
            }
        },
        computed: {
           alerts: function () {
               return this.$refs.alerts
           },
           confirm: function () {
                return this.$refs.confirms
           }
        },
        methods: {
            setStatus: function (menuId, event) {
              let target = event.target;
              if((target.getAttribute('optid') == 4 && !target.checked && !this.getScanCheck(menuId)) || (target.getAttribute('optid') != 4 && target.checked)) {
                   let scan = document.getElementById(menuId + '' + 4);
                   scan.checked = true
              }
              let dom = document.getElementById(menuId + 'P');
              this.getParentCheck(menuId)
            },
             //判断某个菜单全选的选中状态
            getScanCheck: function (menuId) {

                 let dom = document.getElementById(menuId);
                 if(dom) {
                    let inputs = dom.getElementsByTagName('input');
                    if(inputs.length === 0) {
                      return true;
                    } 
                    for (let i = 0; i<inputs.length; i++) {
                       if(inputs[i].getAttribute('optid') != 4) {
                          if (inputs[i].checked){
                             return false;
                          } 
                       } 
                       if( i == inputs.length - 1)  {
                            return true;
                        }
                     }
                 }      
            },
            //判断某个菜单全选的选中状态
            getParentCheck: function (menuId, sign) {
              this.$nextTick(function () {
                 let dom = document.getElementById(menuId);
                 let p = document.getElementById(menuId + 'P');
                 if(dom) {
                    let inputs = dom.getElementsByTagName('input');
                    for (let i = 0; i<inputs.length; i++) {
                       if (!inputs[i].checked) {
                          if(p) {p.checked = false;}
                           return ;
                        } 
                     }
                   if(inputs.length === 0 && p) {
                      p.checked = false;
                   } else {
                      p.checked = true;
                    }
                 }      
              });
             
            },
            //某一菜单下的控制全部选中或取消
            selectAll: function (currentMenu, event) {
               let parent = currentMenu.menuId;
               let allCon = this.permissOpt.perActionData;
               let check = event.target.checked;
               for (let i =0; i<allCon.length;i++) {
                  if(allCon[i].menuId == parent) {
                     let dom = document.getElementById(parent +''+allCon[i].actionId);
                     dom.checked = check;
                     console.log(dom.checked);
                  }
               }
            },
            //判断菜单操作权限
            //optid: 控制权限id ， 有则判断是否显示控制权限，null则判断是否显示菜单
            getCheckByOpt: function (menuId, optid) {
               if(this.permissOpt.menuPei) {
                  for (let i = 0; i < this.permissOpt.menuPei.length; i++) {
                    let item = this.permissOpt.menuPei[i];
                    if(optid && item.menuId === menuId && item.actionId === optid) {
                        return true;
                    } else if (item.menuId === menuId && optid === null){
                      return true;
                    }
                  }
                 return false;
               }  
            },
            //检查菜单的权限
            getCheckMenu: function (menuId) {
                for (let i = 0; i < this.permissOpt.menuPei.length; i++) {
                    let item = this.permissOpt.menuPei[i];
                    if (item.menuId === menuId) {
                        return true;
                    }
                }
                return false;
            },
            getPermissByRoleID: function () {  //根据角色ID请求角色下面的权限信息
                let that = this;
                let param = {roleId: this.roledata.id};
                that.getRequest({
                    url: 'role/queryRolePermissions',
                    method: 'post',
                    param: param,
                    success: function (data) {
                        that.permissOpt.menuPei = data.data;
                    }
                }, true);
            },
            //加载左边的角色
            getRoleInfo: function () {
                let that = this;
                that.getRequest({
                    url: 'role/queryAll',
                    method: 'post',
               
                    success: function (data) {
                        let role = data.data;
                        let roles = [];
                        for (let i =0; i<role.length; i++) {
                            if (role[i].id !== 1) {
                                roles.push(role[i])
                            }
                        }
                        that.roleInfo = roles;
                        //初始化选择
                        that.cancelRoleEdit();
                    }
                });
            },
            //确认删除提示
            delRole: function () {
                let that = this;
                let p = {roleId: this.roledata.id};
                that.getRequest({
                    url: 'role/delete',
                    param: p,
                    method: 'post',
                    success: function (data) {
                        that.roledata = that.cleanSearchData(that.roledata);
                        that.alerts.show({msg: data.message});
                        that.getRoleInfo();//加载角色信息
                    },
                  error: function (data) {
                    that.alerts.show({msg: data.message});
                  }
                }, true);
            },
            //获取角色菜单按钮选择的值
            getSelVal: function () {
               if(document.getElementById('permiss_opt')) {
                  let inputs = document.getElementById('permiss_opt').getElementsByTagName('input');
                  let menuIds = [];
                  for (let i = 0; i<inputs.length; i++) {
                    if (inputs[i].checked && inputs[i].getAttribute("menuId") !== '') {
                        menuIds.push({
                            menuId: parseInt(inputs[i].getAttribute("menuId"), 10),
                            actionId: parseInt(inputs[i].getAttribute("optid"), 10)
                        });
                    }
                  }
                  return menuIds;
               } 
            },
            //选择角色
            selRole: function (roleItem) {
                this.roledata.id = roleItem.id;
                this.roledata.roleName = roleItem.roleName;
                //关闭编辑状态
                this.roleEidtState = false;
                this.permissOpt.editstate = false;
                //加载角色权限
                this.getPermissByRoleID();
            },
            //取消角色配置
            cancelRoleEdit: function () {
                this.roleEidtState = false;
                this.permissOpt.editstate = false;
                let selRole = this.roleInfo[0];
                //选择当前第一个
                this.roledata.id = selRole ? selRole.id : '';
                this.roledata.roleName = selRole ? selRole.roleName : '';

                this.getPermissByRoleID();//重新加载权限
            },
            //修改角色权限视图
           //flag: 是否取消才做的标识
            editRolePeimissView: function (flag) { //打开修改窗口
                let that = this;
                if (flag) {
                    that.cencelRolePeimiss()
                } else {
                  //判断当前是否选择角色，未选中则弹出提示
                  if (that.roledata.id === '') {
                    that.msgShow('请选中要修改的角色');
                  }
                  else {
                    this.permissOpt.editstate = true;
                  }
                }

            },
            //editRole 提交修改绑定事件
            editRolePeimiss: function () { //提交修改角色
                let that = this;
                let reData = that.getSelVal();
                let data = {
                    roleId: that.roledata.id,
                    roleName: this.roledata.roleName,
                    description: '',
                    menuActionList: reData
                };
             //   let reqParam = {strjson: JSON.stringify(data)};
                that.getRequest({
                    url: 'role/update',
                    param: data,
                    method: 'post',
                    success: function (data) {
                      that.getPermissByRoleID();
                      that.permissOpt.editstate = false;
                      that.msgShow(data.message)
                    },
                  error: function (data) {
                    that.msgShow(data.message);
                  }
                })
            },
            //取消提交修改按钮事件
            cencelRolePeimiss: function () {
                this.permissOpt.editstate = false;
                this.roleEidtState = false;
            },
            //请求动作
            getActions: function () {
                let that = this;
                that.getRequest({
                    url: 'menuAction/queryAllMenuAction',
                    method: 'post',
                    success: function (data) {
                        that.permissOpt.perActionData = data.data;
                    }
                })
            },
            //请求菜单
            getMenu: function () {
                let that = this;
                that.getRequest({
      
                  url: 'menu/queryAllEnabledMenus',
                  method: 'post',
                    success: function (data) {
                      let Data = data.data;
                      for(let i=0;i<Data.length;i++) {
                        that.permissOpt.menuData.push(Data[i]);
                      }
                    }
                });
            },
            msgShow: function (msg) {
                this.msgs = msg;
                this.alerts.show({msg: msg});
            },
            //deleteRole 删除角色绑定事件
            deleteRole: function () {
                let that = this;
                if (that.roledata.id === '') {
                    that.state = '0';
                    that.alerts.show({msg: '您还未选择任何角色，不能进行删除！'})
                } else {
                    that.state = '1';
                    that.confirm.show({
                        msg: '您确定要删除选中的角色吗？',
                        action: {
                           confirm: function () {
                           that.delRole();
                           }
                         }
                    });
                }
            },
            //添加角色信息
            addRole: function () {
                if ( this.roledata.roleName.trim() === '') {
                    this.msgShow('角色名称不能为空');
                    return;
                }
                let that = this;
                let reData = that.getSelVal();
                let data = {
                    roleName:  this.roledata.roleName.trim(),
                    description: '',
                    menuActionList: reData
                };
                if(reData.length > 0) {
                  that.getRequest({
                    url: 'role/save',
                    param: data,
                    method: 'post',
                    success: function (data) {
                      that.getRoleInfo();
                      that.cancelRoleEdit();
                      //重新获取角色信息
                      that.msgShow(data.message);
                      //关闭添加框
                    //  that.newRole = '';
                    },
                  error: function (data) {
                    that.msgShow(data.message);
                  }
                });
                } else {
                      this.msgShow('角色权限不能为空');
                }
              //  let reqParam = {strjson: JSON.stringify(data)};

            },
            //打开角色添加视图
            addRoleView: function () {
                this.roleEidtState = true;
                this.permissOpt.editstate = true;
                this.roledata.id = '';
                this.roledata.roleName = '';
                this.permissOpt.menuPei = [];
                //this.newRole = ''
            },
            //页面初始加载内容
            init: function () {
                //获取动作
                this.getActions();
                //获取菜单
                this.getMenu();
                //加载全部角色
                this.getRoleInfo();
            }
        },
        mounted: function () {
            this.isIn();
            this.getAction();
            let that = this;
           
        },
        watch: {
            'roledata.roleName'(val) {
                this.permissOpt.menuPei = this.getSelVal();
            }
        }
    }
</script>
