    li{
      border-bottom: 1px solid rgba(255,255,255,0.15);
      padding-left:0;
      width:100%;
      @extend .customerUl;
      .spanTitle{
        line-height: 3
      }
      .tab_right {
        width:85%;
        padding:0
      }
    }
  }
    .MenuUi::-webkit-scrollbar {
       display:none
    }
  .role-input{
    width: calc(100% - 10em);
    background:#4C5E76;
    display:inline-block
  }
  .button-operate-role{
    height: 2.8em!important;
    border-radius:0!important;
    font-size:0.875em;
    line-height: 1.8;
    margin:0.6em 2em!important;
    .hasImg {
      margin-top:0.1em;
      img{
        width: 1.05em;
        height: 1em;
      }
    }
  }
</style>



